import { FormControl, Box, Stack, TextField, Button } from "@mui/material";

import { useState, useEffect } from "react";
import Tags from "../UI/Tags";
import SymptomSelector from "../Symptom/SymptomSelector";

import useFetch from "../../hooks/useFetch";
import usePost from "../../hooks/usePost";
import { useNavigate } from "react-router";

const INITIAL_DATA = {
  diagnosis: "",
  age: [0, 100],
  tags: "msk",
  symptoms: [],
  associated_symptoms: [],
  minor: []
};

const flattenHashmap = hashMap => {
  return Object.values(hashMap).flatMap(category => Object.values(category));
};

const AddDiagnosis = ({ resource, id }) => {
  const allSymptoms = resource.read();
  const jsonSymptoms = flattenHashmap(allSymptoms);
  const sortedSymptomNames = jsonSymptoms.map(sx => sx.medical_term).sort();
  document.title = id === "add" ? "Add Diagnosis" : "Edit Diagnosis";
  const navigate = useNavigate();
  const { fetchDiagnosis } = useFetch();
  const { addNewDiagnosis, updateDiagnosis } = usePost();

  const [data, setData] = useState(INITIAL_DATA);

  const [didSubmit, setDidSubmit] = useState(false);

  useEffect(() => {
    if (didSubmit) {
      setData(INITIAL_DATA);
      setDidSubmit(false);
    }
  }, [didSubmit]);

  useEffect(() => {
    async function fetchData() {
      const dx = await fetchDiagnosis(id);
      setData(dx);
    }
    if (id !== "add") fetchData();
  }, []);

  async function handleSubmit(e) {
    e.preventDefault();
    if (id === "add") {
      console.log(data);
      await addNewDiagnosis(data);
      setDidSubmit(true);
    } else {
      await updateDiagnosis(id, data);
      navigate("/diagnoses");
    }
  }

  function updateFields(fields) {
    setData(prev => {
      return { ...prev, ...fields };
    });
  }

  return (
    <form onSubmit={handleSubmit}>
      <Stack spacing={2} sx={{ justifyContent: "center", alignItems: "center" }}>
        <Box sx={{ width: "90%", margin: "0 auto" }}>
          <Tags tags={data.tags} updateFields={updateFields} />
        </Box>

        <Box sx={{ width: "90%", margin: "0 auto" }}>
          <FormControl sx={{ width: "100%" }}>
            <TextField
              value={data.diagnosis}
              size="small"
              label="Diagnosis"
              id="diagnosis"
              onChange={e => updateFields({ diagnosis: e.target.value })}
            />
          </FormControl>
        </Box>

        <SymptomSelector
          symptoms={data.symptoms}
          symptomList={sortedSymptomNames}
          jsonSymptoms={jsonSymptoms}
          updateFields={updateFields}
          label="Symptoms"
          id="symptoms"
          didSubmit={didSubmit}
        />
        <SymptomSelector
          symptoms={data.associated_symptoms}
          jsonSymptoms={jsonSymptoms}
          symptomList={sortedSymptomNames}
          updateFields={updateFields}
          label="Associated Symptoms"
          id="associated-symptoms"
          didSubmit={didSubmit}
        />

        <SymptomSelector
          symptoms={data.minor}
          jsonSymptoms={jsonSymptoms}
          symptomList={sortedSymptomNames}
          updateFields={updateFields}
          label="Minor"
          id="minor-symptoms"
          didSubmit={didSubmit}
        />

        <FormControl sx={{ margin: "30px auto" }}>
          <Button variant="contained" type="submit" sx={{ width: "200px" }}>
            {id === "add" ? "Add Diagnosis" : "Update Diagnosis"}
          </Button>
        </FormControl>
      </Stack>
    </form>
  );
};

export default AddDiagnosis;

