import { Box, FormControl, ToggleButtonGroup, ToggleButton } from "@mui/material";

const Tags = ({ tags, updateFields }) => {
  const options = [
    "general",
    "heent",
    "cvs",
    "resp",
    "gi",
    "gu",
    "msk",
    "neuro",
    "obgyn",
    "endo",
    "blood",
    "psych",
    "onco"
  ];

  function handleTagsChange(e, newTags) {
    updateFields({ tags: newTags });
  }

  const control = {
    value: tags,
    onChange: handleTagsChange,
    exclusive: true
  };
  return (
    <Box sx={{ width: "100%", margin: "0 auto", my: "10px" }}>
      <FormControl sx={{ width: "80%" }}>
        <ToggleButtonGroup {...control} aria-label="Tags">
          {options.map(option => (
            <ToggleButton
              value={option}
              key={option}
              sx={{
                height: "3.3vh",
                boxShadow: "rgba(0, 0, 0, 0.16) 0px 1px 4px;"
              }}>
              {option}
            </ToggleButton>
          ))}
        </ToggleButtonGroup>
      </FormControl>
    </Box>
  );
};
export default Tags;
